<template>

    <div class="plan">

        <!-- 搜索区域 -->
        <el-form size="small" :inline="true" :model="searchForm" ref="searchForm">
            <el-form-item label="计划编号" prop="planCode">
                <el-input v-model="searchForm.planCode" placeholder="计划编号"></el-input>
            </el-form-item>
            <el-form-item label="计划状态" prop="status">
                <el-select v-model="searchForm.status" placeholder="计划状态">
                    <el-option label="全部" value=""></el-option>
                    <el-option label="启用" value="normal"></el-option>
                    <el-option label="禁用" value="forbid"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="getData" icon="el-icon-search">查询</el-button>
                <el-button type="warning" @click="reset" icon="el-icon-refresh">重置</el-button>
                <el-button type="success" @click="addVisible = true" icon="el-icon-plus">新增</el-button>
            </el-form-item>
        </el-form>

        <el-divider></el-divider>

        <!-- 表格部分 -->
        <el-table
                size="mini"
                border
                stripe
                v-loading="loading"
                :data="dataList"
                style="width: 100%">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="planCode"
                    label="计划编号"
                    width="350">
                <template slot-scope="scope">
                    <el-link class="planCode" :underline="false" type="primary"
                             @click="lookContent(scope.row.uuid)">{{scope.row.planCode}}
                    </el-link>
                </template>
            </el-table-column>
            <el-table-column
                    prop="description"
                    label="计划名称"
                    width="200">
            </el-table-column>
            <el-table-column
                    prop="srcDb"
                    label="源数据库"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="destDb"
                    label="目标数据库"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="srcProject"
                    label="源系统"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="destProject"
                    label="目标系统"
                    width="150">
            </el-table-column>
            <el-table-column
                    label="启用状态">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.status==='normal'" type="success" size="small">启用</el-tag>
                    <el-tag v-else type="text" size="small">禁用</el-tag>
                </template>
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间">
            </el-table-column>
            <el-table-column
                    prop="updateTime"
                    label="更新时间">
            </el-table-column>
        </el-table>

        <!-- 分页 -->
        <el-pagination
                class="pagination"
                background
                @current-change="currentChange"
                :current-page.sync="currentPage"
                :page-size="pageSize"
                layout="total, prev, pager, next"
                :total="total">
        </el-pagination>

        <!-- 新增 -->
        <el-dialog
                :close-on-click-modal="false"
                title="新增计划"
                :visible.sync="addVisible"
        >
            <el-form :model="addForm" :rules="addRules" ref="addForm">
                <el-form-item prop="planContent">
                    <el-input
                            resize="none"
                            type="textarea"
                            :rows="15"
                            placeholder="请输入内容"
                            v-model="addForm.planContent">
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addCancel">取 消</el-button>
                <el-button :loading="loading" type="primary" @click="add">保 存</el-button>
            </span>
        </el-dialog>

        <!-- 查看 -->
        <el-dialog
                :visible.sync="showVisible"
        >
            <pre v-if="showVisible" v-highlightjs><code class="javascript showContent" v-text="showContent"></code></pre>
        </el-dialog>
    </div>

</template>

<script>
    export default {
        name: "Plan.vue",
        data() {
            return {
                loading: true,
                searchForm: {
                    status: '',
                    planCode: '',
                },
                addForm: {
                    planContent: '',
                },
                addVisible: false,
                showVisible: false,
                showContent: '',
                dataList: [],
                total: 0,
                pageSize: 20,
                currentPage: 1,
                addRules: {
                    planContent: [
                        {required: true, message: '请填写同步计划内容', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            reset() {
                this.$refs["searchForm"].resetFields();
                this.getData();
            },
            getData() {
                this.loading = true;
                const params = {};
                Object.keys(this.searchForm).forEach(key => {
                    params[key] = this.searchForm[key];
                })
                params.page = this.currentPage;
                params.limit = this.pageSize;
                this.$sendAjax.doGet('/sync/plan/page', {params}).then((data) => {
                    this.dataList = data.result.dataList;
                    this.total = data.result.totalRow;
                }).finally(() => {
                    this.loading = false;
                });
            },
            currentChange(current) {
                this.currentPage = current;
                this.getData();
            },
            add() {
                this.$refs['addForm'].validate((valid) => {
                    if (valid) {
                        this.loading = true;

                        this.$sendAjax.doPost('/sync/plan/save',this.addForm , { emulateJSON: false }).then(({data:res})=>{

                            this.addVisible = false;
                            this.$message.success("新增成功!");
                            this.$refs["addForm"].resetFields();
                            this.getData();

                        }).finally(()=>{

                            this.loading=false;
                        })


                    } else {
                        return false;
                    }
                });

            },
            addCancel() {
                this.$refs["addForm"].resetFields();
                this.addVisible = false;
            },
            lookContent(uuid) {

                this.$sendAjax.doGet('/sync/plan/content?uuid='+uuid).then((data) => {

                    this.showContent = data.result;
                    this.showVisible = true;
                }).finally(() => {

                });
            }
        },
        mounted() {
            this.getData();
        }
    }
</script>

<style scoped>
    .planCode {
        font-family: Consolas, serif;
    }

    .pagination {
        margin-top: 20px;
    }
    .showContent{
        font-family: "Ubuntu Mono",serif!important;
    }
</style>